export default {
	props: {
		bookID:{
			type: Number,
			default: 0
		},
		bookImgUrl: {
			type: String,
			default: ''
		},
		bookName: {
			type: String,
			default: ''
		},
		bookDesc: {
			type: String,
			default: ''
		}
	},
	methods: {
		init(){
			this.showCancelCol(true);
			this.throwID(this.bookID)
		},
		throwID(id){
			this.$emit('getID', id)
		},
		showCancelCol(bol){
			this.$emit('showCancelCol', bol)
		}
	},
}
